<script>
import { getFlowerRank } from "../service/flower";
import servers from "@jx3box/jx3box-data/data/server/server_list.json";
import flower_types from "../assets/data/flower_types.json";
import {setServer,getServer} from '../service/server'

export default {
    name: "list_side",
    props: [],
    data: function() {
        return {
            focus: 0,
            subnav: ["趋势榜", "7日下载榜", "30日下载榜"],
            data: [],
            loading: false,
            server: "",
            servers,
        };
    },
    computed: {},
    methods: {
        loadPrice(server) {
            this.loading = true;
            getFlowerRank(server).then((res) => {
                let data = res.data;
                let list = [];
                flower_types.forEach((name) => {
                    list.push({
                        name,
                        line: data[name]["line"],
                        price: ~~data[name]["price"],
                    });
                });
                this.data = list;
                this.loading = false;
            });
        },
        changeServer() {
            setServer(this.server)
            this.loadPrice(this.server);
        },
    },
    mounted: function() {
        getServer().then((server) => {
            if(server){
                this.server = server
                this.loadPrice(server);
            }else{
                this.loadPrice("梦江南");
            }
        })
    },
    components: {},
};
</script>

